*{
    box-sizing: border-box;
}
body{
    margin: 0;
}
h1, h2, h3, h4, h5, h6{
    margin: 0;
    font-weight: 400;
}
figure{
    margin: 0;
}
img{
    vertical-align: top;
}
ul{
    list-style: none;
    padding: 0;
    margin: 0;
}
a{
    text-decoration: none;
}
.center-grid{
    display: flex;
    justify-content: center;
    .grid{
        width: $grid-width;
    }
    .grid--small{
        width: $grid-small;
    }
}